.dollar-token-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.dt-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.95);
  padding: 20px 30px;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;

  .header-left {
    h1 {
      margin: 0;
      font-size: 28px;
      color: #333;
    }

    .subtitle {
      margin: 5px 0 0;
      color: #666;
      font-size: 14px;
    }
  }

  .header-right {
    display: flex;
    align-items: center;
    gap: 15px;

    .user-name {
      font-weight: 600;
      color: #333;
    }

    .btn-logout {
      padding: 8px 20px;
      background: #ff4757;
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        background: #e84118;
        transform: translateY(-2px);
      }
    }
  }
}

.dt-main-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  max-width: 1400px;
  margin: 0 auto;

  @media (max-width: 1024px) {
    grid-template-columns: 1fr;
  }
}

.wallet-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);

  .wallet-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;

    h2 {
      margin: 0;
      font-size: 24px;
      color: #333;
    }

    .wallet-address {
      font-size: 12px;
      color: #666;
      cursor: pointer;
      padding: 8px 12px;
      background: #f8f9fa;
      border-radius: 8px;
      transition: all 0.3s ease;

      &:hover {
        background: #e9ecef;
      }

      .copy-icon {
        margin-left: 5px;
      }
    }
  }

  .wallet-balance {
    text-align: center;
    padding: 30px 0;
    border-top: 2px solid #f0f0f0;
    border-bottom: 2px solid #f0f0f0;
    margin-bottom: 30px;

    .balance-label {
      margin: 0 0 10px;
      color: #888;
      font-size: 14px;
    }

    .balance-amount {
      margin: 0;
      font-size: 48px;
      font-weight: 700;
      color: #667eea;
    }

    .balance-usd {
      margin: 10px 0 0;
      color: #999;
      font-size: 16px;
    }
  }

  .wallet-actions {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;

    .btn-action {
      padding: 15px 10px;
      border: none;
      border-radius: 12px;
      cursor: pointer;
      font-size: 14px;
      font-weight: 600;
      transition: all 0.3s ease;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 8px;

      .icon {
        font-size: 24px;
      }

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
      }

      &.btn-send {
        background: linear-gradient(135deg, #667eea, #764ba2);
        color: white;
      }

      &.btn-receive {
        background: linear-gradient(135deg, #5f27cd, #341f97);
        color: white;
      }

      &.btn-exchange {
        background: linear-gradient(135deg, #00d2ff, #3a47d5);
        color: white;
      }
    }
  }
}

.transactions-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);

  h2 {
    margin: 0 0 20px;
    font-size: 24px;
    color: #333;
  }

  .transactions-list {
    max-height: 500px;
    overflow-y: auto;

    .no-transactions {
      text-align: center;
      padding: 60px 20px;
      color: #999;
    }

    .transaction-item {
      display: flex;
      align-items: center;
      gap: 15px;
      padding: 15px;
      border-bottom: 1px solid #f0f0f0;
      transition: all 0.3s ease;

      &:hover {
        background: #f8f9fa;
      }

      &.pending {
        opacity: 0.6;
      }

      .tx-icon {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 24px;
        background: #f8f9fa;

        &.send {
          background: #ffe0e0;
        }

        &.receive {
          background: #e0f7e0;
        }
      }

      .tx-info {
        flex: 1;

        .tx-type {
          margin: 0 0 5px;
          font-weight: 600;
          color: #333;
        }

        .tx-date {
          margin: 0;
          font-size: 12px;
          color: #999;
        }
      }

      .tx-amount {
        text-align: right;

        .amount {
          margin: 0 0 5px;
          font-size: 18px;
          font-weight: 700;

          &.positive {
            color: #27ae60;
          }

          &.negative {
            color: #e74c3c;
          }
        }

        .status {
          margin: 0;
          font-size: 12px;
          padding: 3px 8px;
          border-radius: 10px;
          display: inline-block;

          &.completed {
            background: #d4edda;
            color: #155724;
          }

          &.pending {
            background: #fff3cd;
            color: #856404;
          }
        }
      }
    }
  }
}

// 모달 스타일
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;

  .modal-content {
    background: white;
    padding: 30px;
    border-radius: 15px;
    max-width: 500px;
    width: 90%;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);

    h3 {
      margin: 0 0 20px;
      font-size: 24px;
      color: #333;
    }

    p {
      margin: 0 0 20px;
      color: #666;
    }

    .btn-close {
      width: 100%;
      padding: 12px;
      background: #667eea;
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      font-size: 16px;
      font-weight: 600;
      transition: all 0.3s ease;

      &:hover {
        background: #5568d3;
      }
    }
  }
}
